.add-button{
  background-color: #c2fbd7;
  border-radius: 100px;
  box-shadow: rgba(44, 187, 99, .2) 0 -25px 18px -14px inset,rgba(44, 187, 99, .15) 0 1px 2px,rgba(44, 187, 99, .15) 0 2px 4px,rgba(44, 187, 99, .15) 0 4px 8px,rgba(44, 187, 99, .15) 0 8px 16px,rgba(44, 187, 99, .15) 0 16px 32px;
  color: green;
  cursor: pointer;
  display: flex;
  margin: 3rem auto;
  font-family: CerebriSans-Regular,-apple-system,system-ui,Roboto,sans-serif;
  padding: 7px 20px;
  text-align: center;
  transition: all 250ms;
  border: 0;
  font-size: 1.5rem;
  z-index: 1;
}

.add-button:hover {
  box-shadow: rgba(44,187,99,.35) 0 -25px 18px -14px inset,rgba(44,187,99,.25) 0 1px 2px,rgba(44,187,99,.25) 0 2px 4px,rgba(44,187,99,.25) 0 4px 8px,rgba(44,187,99,.25) 0 8px 16px,rgba(44,187,99,.25) 0 16px 32px;
  transform: scale(1.05) rotate(-1deg);
}

.add-button__cancel{
  background-color: #fc7d7d;
  color: rgb(138, 0, 0);
  box-shadow: rgba(187, 44, 99, 0.2) 0 -25px 18px -14px inset,rgba(187, 44, 151, 0.2) 0 1px 2px,rgba(187, 44, 151, 0.2) 0 2px 4px,rgba(187, 44, 151, 0.2) 0 4px 8px,rgba(187, 44, 151, 0.2) 0 8px 16px,rgba(187, 44, 151, 0.2) 0 16px 32px;
}
.add-button__cancel:hover {
  box-shadow: rgba(187, 44, 99, 0.2) 0 -25px 18px -14px inset,rgba(187, 44, 99, 0.2) 0 1px 2px,rgba(187, 44, 99, 0.2) 0 2px 4px,rgba(4187, 44, 99, 0.2) 0 4px 8px,rgba(4187, 44, 99, 0.2) 0 8px 16px,rgba(4187, 44, 99, 0.2) 0 16px 32px;
  transform: scale(1.05) rotate(-1deg);
}